@import '../../globalVariable';

$loadingSize: 20px;

.vercodeInput {
  display: flex;
  .inputWrapper {
    flex: 1;
  }
  .textWrapper {
    display: flex;
    flex: 1;
    justify-content: center;
    align-items: center;
    height: 44px;
    font-size: $middleFontSize;
    .text {
      line-height: 100%;
      &.normalText {
        cursor: pointer;
      }
      &.countingText {
        color: $fontColor;
      }
      @include hide;
    }
    .loadingIconWrapper {
      width: $loadingSize;
      height: $loadingSize;
      @include hide;
    }
  }
}
